const utils = require('./utils.js');

function getRanks() {
    const clientPromise = stitch.StitchClientFactory.create('wwe-champions-guide-pgwcj');
    clientPromise.then(client => {
        const db = client.service('mongodb', 'mongodb-atlas').db('wwe-champions');
        client.login().then(() => 
            db.collection('players').aggregate([
                {"$unwind": "$feudScores"},
                {"$match": {"feudScores.rank": {"$lte": 100}, "feudScores.ffID": "FF20180720"}},
                {"$sort":{"feudScores.rank": 1}}
            ])
        ).then(docs => {
            hideLoading();
            if (docs) {
                console.log(docs);
                displayResult(docs);
            } else {
                console.log("No result");
                displayError();
            }
            console.log("[MongoDB Stitch] Connected to Stitch")
        }).catch(err => {
            console.error("Invalid request");
            console.error(err);
            hideLoading();
            displayError();
        });
    });
}

function hideLoading() {
    $("#loading").hide();
}

function displayError() {
    $("#results").empty();
    $("#results").text("Error loading!");
}

function displayResult(items) {
    var $tablebody = $("<tbody/>");
    items.forEach(function(item){
        $tablebody.append(
            $("<tr/>")
            .append(
                $("<td/>")
                .append(
                    $("<a/>", {href: "/stats/players/profile.html?id=" + item._id.toString(), text: item.name})
                )
            )
            .append(
                $("<td/>", {text: item.feudScores.faction})
            )
            .append(
                $("<td/>", {text: item.feudScores.score})
            )
            .append(
                $("<td/>", {text: item.feudScores.rank})
            )
        )
    });

    $("#results").empty();
    $("#results").append($("<h3/>", {text: "Top 100 Feud Players (June 9 2018)"}));
    $("#results")
    .append(
        $("<table/>", {class: "table"})
        .append(
            $("<thead/>")
            .append(
                $("<tr/>")
                .append(
                    $("<th/>", {text: "Name"})
                )
                .append(
                    $("<th/>", {text: "Faction"})
                )
                .append(
                    $("<th/>", {text: "Score"})
                )
                .append(
                    $("<th/>", {text: "Rank"})
                )
            )
        )
        .append(
            $tablebody
        )
    )
}

$(document).ready(function () {
    getRanks();
});


